                    align-content: space-around;
                    position: absolute;
                    top: 0;
                    z-index: 5;
                    li{
                      min-height: 40px;
                      display: flex;
                      position: relative;
                      span{
                        margin: 0 10px;
                        display: inline-block;
                        font: 1.75em/1.4em "Arial";
                        white-space: nowrap;
                        text-transform: uppercase;
                      }
                    }
                    .companyName{
                      left: -185px;
                      span{
                        font-weight: bold;
                        color: #18ff00;
                      }
                    }
                    .security{
                      left: -25px;
                      span{
                        color: #ff0000;
                      }
                    }
                  }
                  .layer-2{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    flex-wrap: nowrap;
                    justify-content: flex-start;
                    align-content: space-around;
                    position: absolute;
                    top: 0;
                    left: -120px;
                    transform: rotate(-45deg);
                    z-index: 10;
                    li{
                      margin: 30px 0;
                      display: flex;
                      flex-wrap: nowrap;
                      position: relative;
                      top: -65px;
                      span{
                        margin: 0 25px;
                        display: inline-block;
                        font: 1.25em/1.5em "Arial";
                        color: #002aff;
                        white-space: nowrap;
                      }
                      &:nth-child(odd){
                        margin: 30px 0 30px 80px;
                      }
                    }
                  }
                  .layer-3{
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    z-index: 15;
                    .bg{
                      width: 100%;
                      height: 100%;
                      display: block;
                      ul{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        align-items: center;
                        li{
                          display: flex;
                          justify-content: center;
                          align-items: center;
                          &:first-child{
                            transform: rotate(-90deg);
                          }
                          &:last-child{
                            span{
                              font-size: 1.25em;
                            }
                          }
                        }
                        .userLogo{
                          width: 33%;
                          img{
                            margin: 10px;
                            width: 110px;
                            height: 115px;
                            position: relative;
                            box-shadow: none;
                          }
                        }
                        .userText{
                          width: 33%;
                          span{
                            padding: 0 0 0 5px;
                            display: inline-block;
                            font: 2.25em/1.25em "Arial";
                            font-weight: bold;
                            color: #888;
                            white-space: nowrap;
                          }
                        }
                      }
                    }
                  }
                }
              }
              .hologramOptions{
                width: 50%;
                .layer-1,
                .layer-2,
                .layer-3{
                  h6{
                    margin: 15px 0;
                    padding: 0 0 0 15px;
                    font: 1.25em/35px "Arial";
                    font-weight: bold;
                    text-transform: uppercase;
                    background-color: #d9d9d9;
                  }
                  .laserNumbering,
                  .laserLogo,
                  .laserText{
                    padding: 7.5px 0;
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    div{
                      margin: 7px 0;
                    }
                    .name{
                      input{
                        margin: 0 5px 0 0;
                        padding: 0 0 0 20px;
                        width: 220px;
                        height: 40px;
                        font: 1.063em/40px "Arial";
                        color: #000;
                        border: 1px solid #d0d0d0;
                      }
                    }
                    .i-arrows,
                    .i-arrows-alt,
                    .i-refresh{
                      width: 40px;
