                      margin: 0 5px;
                      text-align: center;
                      background-color: #427b94;
                      border: 1px solid #d0d0d0;
                      i{
                        font-size: 2em;
                        line-height: 40px;
                        color: #fff;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                    .fontSize{
                      margin: 0 0 0 125px;
                      width: 175px;
                      display: flex;
                      align-items: center;
                      p{
                        margin: 0 0 0 15px;
                        font: 1.063em/40px "Arial";
                      }
                      input{
                        margin: 0 5px 0 8px;
                        width: 40px;
                        height: 40px;
                        font: 1.063em/40px "Arial";
                        text-align: center;
                        text-transform: uppercase;
                        border: 1px solid #d0d0d0;
                      }
                      span{
                        display: flex;
                        flex-direction: column;
                        i{
                          color: #427b94;
                          &:hover{
                            cursor: pointer;
                          }
                        }
                      }
                    }
                    .fontWeight{
                      display: flex;
                      align-items: center;
                      p{
                        margin: 0 8px 0 0;
                        font: 1.063em/40px "Arial";
                      }
                      input[type="checkbox"]{
                        display: none;
                      }
                      input[type="checkbox"] + span:before{
                        font-family: "FontAwesome";
                      }
                      input[type="checkbox"]:checked + span:before{
                        content: "\f111";
                        font-size: 10px;
                        line-height: 20px;
                        color: #6d6d6d;
                      }
                      label{
                        width: 20px;
                        height: 20px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        background-color: #fff;
                        border: 1px solid #d0d0d0;
                        border-radius: 50px;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                  .selectFile{
                    padding: 7.5px 0 15px;
                    display: flex;
                    input{
                      width: 0.01px;
                      height: 0.01px;
                      opacity: 0;
                      overflow: hidden;
                      z-index: -1;
                    }
                    input + label{
                      margin: 0 65px 0 0;
                      min-width: 160px;
                      display: block;
                      font: 1.063em/40px "Arial";
