                      border: 1px solid #d0d0d0;
                      i{
                        font-size: 2em;
                        line-height: 40px;
                        color: #fff;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                }
              }
            }
            .backOrForward{
              display: flex;
              justify-content: space-around;
              a{
                margin: 45px 0 35px;
                padding: 0 35px;
                width: 315px;
                display: block;
                font: 1.25em/40px "Arial";
                color: #fff;
                text-align: center;
                text-transform: uppercase;
                text-decoration: none;
                background-color: #009fff;
              }
            }
          }
        }
        .col-sm-12{
          .aside{
            margin: 50px 0 0;
            display: none;
            &__vhLogo{
              display: none;
            }
          }
        }
      }
    }
  }
}